<template>
  <div class="SDishOperation">
    <headerUsually btnShow="true" titleText="单菜操作" url="/mobileOrdering/myOrder"></headerUsually>
    <div class="container-main">
      <div class="alreadyOrder" v-if="modifySDish.pluState==1">
        <div class="item" v-for="(item,index) in list" :key="index" @click="goFun(item)">
          <span class="text">{{item.text}}</span>
          <i class="iconfont icon-jiantou1"></i>
        </div>
        <template v-if="modifySDish.flag.indexOf('1')==2">
          <div class="item" @click="goFun({url:tempUrl})">
            <span class="text">临时菜操作</span>
            <i class="iconfont icon-jiantou1"></i>
          </div>
        </template>
        <template v-if="modifySDish.flag.indexOf('1')==0 &&modifySDish.accnt">
          <div class="item" @click="goFun({url:packageUrl})">
            <span class="text">套餐操作</span>
            <i class="iconfont icon-jiantou1"></i>
          </div>
        </template>
        <!-- <div ></div> -->
      </div>
      <div class="noOrder" v-else>
        <div class="item" v-if="modifySDish.number>0" @click="returnDishFun('returnDish')">
          <span class="text">退菜操作</span>
          <i class="iconfont icon-jiantou1"></i>
        </div>
        <div class="item" @click="returnDishFun('reversalDish')">
          <span class="text">冲销操作</span>
          <i class="iconfont icon-jiantou1"></i>
        </div>
        <!-- <template v-if="modifySDish.flag.indexOf('1')==2">
          <div class="item" @click="goFun({url:tempUrl})">
            <span class="text">临时菜操作</span>
            <i class="iconfont icon-jiantou1"></i>
          </div>
        </template>-->
        <template v-if="modifySDish.flag.indexOf('1')==1 || modifySDish.flag.indexOf('1')==2">
          <div class="item" @click="goFun({url:list[0].url})">
            <span class="text">更改数量</span>
            <i class="iconfont icon-jiantou1"></i>
          </div>
        </template>
        <template v-if="modifySDish.flag.indexOf('1')==0">
          <div class="item" @click="goFun({url:packageUrl})">
            <span class="text">套餐操作</span>
            <i class="iconfont icon-jiantou1"></i>
          </div>
        </template>
      </div>
    </div>
  </div>
</template>
<script>
import ajaxGet from "@/conf/ajaxGet";
import { MessageBox, Toast } from "mint-ui";
import headerUsually from "@/components/common/mobileOrdering/headerUsually";
import { mapState, mapGetters } from "vuex";
export default {
  components: {
    headerUsually
  },

  data() {
    return {
      list: [
        {
          text: "更改数量",
          url: "/mobileOrdering/changeQuantity"
        },
        { text: "删除菜项", url: "" },
        { text: "口味烧法", url: "/mobileOrdering/tasteMethod" }
      ],
      tempUrl: "/mobileOrdering/tempDishModify",
      packageUrl: "/mobileOrdering/packageOperation"
    };
  },
  computed: {
    ...mapState({
      modifySDish: state => state.mobileOrdering.modifySDish
    })
  },
  created() {
    console.log("modifySDish:", this.modifySDish);
  },
  methods: {
    goFun(item) {
      if (item.url) {
        this.$router.push(item.url);
      } else {
        MessageBox.confirm("是否确认删除菜项？")
          .then(async action => {
            if (this.modifySDish.accnt) {
              let data = {
                params: {
                  proType: 2,
                  pluState: this.modifySDish.pluState,
                  accnt: this.modifySDish.accnt,
                  orderId: this.modifySDish.id
                }
              };

              let res = await ajaxGet(
                "/pos/router?method=updateCheckDetail",
                data
              );
              if (res && res.resultCode == 0) {
                this.msg("删除成功！");
                this.$router.push("/mobileOrdering/myOrder");
              }
            } else {
              this.$store.commit("changeTempTable", {
                type: "delete",
                item: this.modifySDish
              });
              this.$router.push("/mobileOrdering/myOrder");
            }
          })
          .catch(action => {});
      }
    },
    async returnDishFun(type) {
      //this.$router.push("/mobileOrdering/returnDish");
      if (type == "reversalDish") {
        let data = {
          params: {
            authCode: "pos!posOrder!writeOff"
          }
        };
        let res = await ajaxGet("/pos/router?method=checkAuthExist", data);
        if (res && res.resultCode == 0) {
          if (res.data == "true") {
            this.$router.push({
              name: "mobileOrderingReturnDish",
              params: {
                type: type
              }
            });
          } else {
            this.msg("没有冲销权限！");
          }
        }
      } else {
        this.$router.push({
          name: "mobileOrderingReturnDish",
          params: {
            type: type
          }
        });
      }
    },
    msg(msg) {
      Toast({
        message: msg,
        position: "middle",
        duration: 3000
      });
    }
  }
};
</script>
<style lang="less" scoped>
.SDishOperation {
  padding-top: 46px;
  .container-main {
    padding-left: 15px;
    background-color: #fff;
    .item {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 17px 15px 17px 0;
      border-bottom: 1px solid #ddd;
      font-size: 14px;
      color: #000000;
      .iconfont {
        color: #a8acb8;
      }
    }
    .item:last-child {
      border-bottom: none;
    }
  }
}
</style>




